.task_holder > img {
    text-align: center;
    width: 215px;
    height: 234px;
    margin-left: 106px; }
.task {
    display: grid;
    grid-template-columns: 369px 59px;
    color: white;
    .task_info {
        background-color: #34495e;
        margin-bottom: 14px;
        border-radius: 5px 0 0 5px;
        & > p {
            display: flex;
            justify-content: flex-start;
            align-items: center;
            height: 59px;
            padding: 14px 18px;
            margin: 0;
            box-sizing: border-box; }
        .task_note {
            font-size: 18px;
            font-style: italic; } }
    .task_checked {
        .done {
            background-image: url("../../media/done_mark.png");
            background-position: center;
            background-repeat: no-repeat;
            height: 59px;
            border: 2px solid #27ae60;
            border-radius: 0 5px 0 0;
            box-sizing: border-box;
            cursor: pointer; }
        .d_active {
            background-color: #27ae60;
            background-image: url("../../media/done_mark.png");
            background-position: center;
            background-repeat: no-repeat;
            height: 59px;
            border: 2px solid #27ae60;
            border-radius: 0 5px 0 0;
            box-sizing: border-box;
            cursor: pointer; }
        .canceled {
            background-image: url("../../media/canceled_mark.png");
            background-position: center;
            background-repeat: no-repeat;
            height: 59px;
            border: 2px solid #e74c3c;
            border-radius: 0 0 5px 0;
            box-sizing: border-box;
            cursor: pointer; }
        .c_active {
            background-color:  #e74c3c;
            background-image: url("../../media/canceled_mark.png");
            background-position: center;
            background-repeat: no-repeat;
            height: 59px;
            border: 2px solid #e74c3c;
            border-radius: 0 0 5px 0;
            box-sizing: border-box;
            cursor: pointer; } } }
